@font-face {
    font-family: 'Comfortaa';
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/comfortaa/v37/1Pt_g8LJRfWJmhDAuUsSQamb1W0lwk4S4TbMDr4fIA9c.woff2) format('woff2');
    unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
  }

.infoDate {
    display: flex;
    flex-flow: row wrap;
}

.fields {
    padding: 10px;
    margin: 10px;
    font-family: 'Comfortaa';
}

@media (max-width: 768px) {
    .infoDate {
        display: flex;
        flex-direction: column;
        width: 70%;
        margin: 10px auto;
    }

}